.product {
  &__card {
    display: grid;
    grid-auto-flow: column;
    column-gap: 5em;
    justify-content: space-between;
    padding: 2em 0;

    &_images_wrapper {
      position: relative;
      width: max-content;
      height: max-content;

      .product__out-of-stock {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-weight: 300;
        font-size: 40px;
        margin: 0;
        text-align: center;
        color: #8d8f9a;
      }

      .product_images {
        &--out-of-stock {
          mask-image: linear-gradient(rgba(0, 0, 0, 0.096), rgba(0, 0, 0, 0.26));
        }
  
        &--main {
          height: 320px;
          width: 320px;
          object-fit: contain;
          cursor: pointer;
        }
  
        &--all {
          display: flex;
          justify-content: space-evenly;
  
          .image {
            object-fit: contain;
            height: 50px;
            width: 50px;
            cursor: pointer;
          }
        }
      }
    }

    &_content {
      display: grid;
      row-gap: 2em;

      &_info {
        display: grid;
        row-gap: 2em;

        .info--name {
          font-weight: bold;
          position: relative;

          &::after {
            content: "";
            position: absolute;
            top: 2em;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: gray;
          }
        }

        .info_block {
          display: grid;
          grid-auto-flow: column;
          justify-content: space-between;
          align-items: center;

          &--price {
            font-size: 1.5em;
            font-weight: bold;
          }

          &--color {
            width: 1.5em;
            height: 1.5em;
          }
        }

        .info--description {
          font-weight: 300;
        }
      }

      &_specs {
        display: grid;
        row-gap: 2em;

        &_details {
          display: grid;
          grid-auto-flow: column;
          justify-content: space-between;

          &_block {
            &--help {
              margin: 0;
              padding: 0;
              font-weight: 300;
              font-size: 0.85em;
            }

            &--quantity, &--addInfo {
              padding: 0.3em;
            }

            &--quantity {
              width: 20%;
              font-weight: bold;
              font-size: 2em;
            }
          }
        }

        &_button {
          width: 50%;
          justify-self: center;
          font-size: 2em;
          font-weight: bold;
          padding: 0.5em;
          background-color: rgba(255, 255, 0, 0.675);
          cursor: pointer;

          &:hover {
            background-color: yellow;
          }

          &:active {
            background-color: blue;
          }
        }
      }
    }
  }
}
